.btn {
  display: inline-block;
  font: 600 15px/22px "Poppins", sans-serif;
  padding: 16px 14px;
  width: 264px;
  text-align: center;
  background-color: $acsent-color;
  border-radius: 8px;
  color: $ligth-color;
  text-decoration: none;
  border: 1px solid transparent;
  transition: all 0.3s;

  @include sm-mobile {
    min-width: 100%;
  }

  &--top {
    padding: 14px 14px 13px;
    line-height: 21px;

  }

  &:hover {
    /* color: $acsent-color; */
    background-color: $acsent-form-color;
  }
}
