.form {
  grid-column: 1 / 6;

  &__block {
    display: flex;
    flex-direction: column;
  }

  &__title {
    color: $ligth-color;
    margin-bottom: 56px;

    @include mobile {
      margin-bottom: 48px;
      line-height: 41.6px;
    }
  }

  &__btn {
    margin-top: 80px;

    @include mobile {
      margin-top: 56px;
    }
  }

  &__field {
    outline: none;
    border: none;
    padding-bottom: 17px ;
    resize: none;
    font: 600 15px/22px "Poppins", sans-serif;
    color: $ligth-color;
    transition-duration: 0.3s;
    border-bottom: 1px solid $ligth-color;
    background-color: transparent;

    &:not(:first-child) {
      margin-top: 48px;

      @include mobile {
        margin-top: 40px;
      }
    }

    &:focus {
      border-color: $acsent-form-color;
    }

    &::placeholder {
      font: 600 15px/22px "Poppins", sans-serif;
      color: $ligth-color;
      opacity: 0.7;
    }

    @include tablet {
      max-width: 362px;
    }

    @include laptop {
      max-width: 370px;
    }
  }
}
