.title {
  font: 600 32px/48px "Poppins", sans-serif;

  &--l {
    font-size: 52px;
    line-height: 78px;

    @include tablet {
      line-height: 62.4px;
    }

    @include mobile {
      font-size: 44px;
      line-height: 52.8px;
    }
  }

  &--light {
    font-weight: 700;
    font-size: 13px;
    line-height: 19.5px;
    letter-spacing: 3px;
    text-transform: uppercase;
  }
}
